import React from 'react';
import './Detail.css'

class Detail extends React.Component {
    componentDidMount() {
        const { location, history } = this.props;
        if(location.state === undefined){
            history.push('/');
        }
    }
    render(){
        const {location} = this.props;
        if(location.state) {
            const { year, title, summary, poster,genres} = location.state
            return (
                <div className='detail'>
                    <img src={poster} alt={title} title={title}/>
                    <div className='detail__data'>
                        <h3 className='detail__title'>{title}</h3>
                        <h5 className='detail__year'>{year}</h5>
                        <ul className='detail__genres'>
                            {genres.map((gnere, index ) => {
                            return ( <li key={index} className='detail__genres__genre '>{gnere}</li>)
                            })}
                        </ul>
                        <p className='detail__summary'>{summary}</p>
                    </div>
                </div>
            );
        }
        else {return null}
    }
}

export default Detail;